import React from "react";
import Card from "../../../components/card";
import SummaryCardHeader from "./summary-card-header";
import IconLabel from "../../../components/icon-label";
import CheckmarkIcon from "../../../components/svg/checkmark";
import PriceDetails from "./price-details";
import Button from "../../../components/inputs/button";
import { Link } from "react-router-dom";
import {
  getNoOfDays,
  formatPhoneForDisplay,
  getPrices
} from "coolcamper-common";
import Loader from "../../../components/loader";

export const Name = "Sumar";
export default class extends React.Component {
  constructor(props) {
    super(props);
    window.ga("send", "event", {
      eventCategory: "New booking",
      eventAction: "view booking step",
      eventLabel: "step 3"
    });
  }

  render() {
    const {
      startDate,
      endDate,
      firstName,
      lastName,
      email,
      phone,
      isLoading,
      submitError,
      onSubmit
    } = this.props;
    const noOfDays = getNoOfDays(startDate, endDate);
    const prices = getPrices(startDate, endDate);

    return (
      <div className="step__three">
        <Card
          className="step__three__card step__three__card--type"
          renderHeader={() => (
            <SummaryCardHeader title="Tip autorulot i perioada" />
          )}
          renderBody={() => (
            <React.Fragment>
              <p>Benimar Tessoro 495</p>
              <p>5 locuri de dormit + 5 pe scaune</p>
              <p>
                <strong>
                  {noOfDays} zile - {startDate.format("D MMM YYYY")} &rarr;{" "}
                  {endDate.format("D MMM YYYY")}
                </strong>
              </p>
            </React.Fragment>
          )}
        />
        <Card
          className="step__three__card step__three__card--contactData"
          renderHeader={() => <SummaryCardHeader title="Date de contact" />}
          renderBody={() => (
            <React.Fragment>
              <p>
                {firstName} {lastName}
              </p>
              <p>{formatPhoneForDisplay(phone)}</p>
              <p>{email}</p>
            </React.Fragment>
          )}
        />
        <Card
          className="step__three__card step__three__card--benefits"
          renderHeader={() => <SummaryCardHeader title="Beneficii incluse" />}
          renderBody={() => (
            <React.Fragment>
              <IconLabel
                text="Asigurare full CASCO"
                icon={CheckmarkIcon}
                iconWidth={16}
              />
              <IconLabel text="Rovignet" icon={CheckmarkIcon} iconWidth={16} />
              <IconLabel
                text="Numr nelimitat de km"
                icon={CheckmarkIcon}
                iconWidth={16}
              />
              <IconLabel
                text="Suport de biciclete"
                icon={CheckmarkIcon}
                iconWidth={16}
              />
              <IconLabel
                text="Mas i scaune de camping"
                icon={CheckmarkIcon}
                iconWidth={16}
              />
              <IconLabel
                text="Aternuturi, perne i lenjerii de pat"
                icon={CheckmarkIcon}
                iconWidth={16}
              />
              <IconLabel
                text="Prosoape de baie"
                icon={CheckmarkIcon}
                iconWidth={16}
              />
              <IconLabel
                text="Vesel, oale i tigi pentru gtit"
                icon={CheckmarkIcon}
                iconWidth={16}
              />
              <IconLabel
                text="Anularea rezervrii este gratuit, oricnd"
                icon={CheckmarkIcon}
                iconWidth={16}
              />
            </React.Fragment>
          )}
        />
        <Card
          className="step__three__card step__three__card--price"
          renderHeader={() => <SummaryCardHeader title="Pre" />}
          renderBody={() => (
            <React.Fragment>
              <PriceDetails
                startDate={startDate}
                endDate={endDate}
                prices={prices}
              />
              <div className="step__three__card__paymentMethods">
                <div className="step__three__card__paymentMethods--left">
                  <p>
                    <strong>Modalitate de plat</strong>
                  </p>
                  <small>Prin transfer bancar</small>
                  <small>Avans: 30% n max. 2 zile dup rezervare</small>
                  <small>Restul de 70%: cu 15 zile nainte de plecare</small>
                </div>
                <div className="step__three__card__paymentMethods--right">
                  <p>
                    <strong>Anulare gratuit</strong>
                  </p>
                  <small>
                    n cazul anulrii rezervrii, suma pltit va fi returnat
                    integral, n maximum 15 zile lucrtoare.
                  </small>
                </div>
              </div>
            </React.Fragment>
          )}
        />
        <Card
          className="step__three__card step__three__card--general"
          renderBody={() => (
            <div className="step__three__card__sendReservation">
              <h3>
                Total de plat: {prices.totalPriceWithDiscount.toLocaleString()}
                
              </h3>
              <small>
                Prin plasarea comenzii, eti de acord cu{" "}
                <Link
                  to="/termeni-si-conditii"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Termenii i Condiiile
                </Link>
                , cu{" "}
                <Link
                  to="/politica-de-confidentialitate"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Politica de Confidenialitate i Utilizare a cookie-urilor
                </Link>{" "}
                i cu{" "}
                <Link
                  to="/anulare-rezervare"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Politica de Anulare
                </Link>
              </small>
              <div className="step__three__card__sendReservation--submit">
                <Button
                  type="primary"
                  onClick={() => onSubmit(prices)}
                  className={isLoading ? "loading" : ""}
                  ga-on="click"
                  ga-event-category="Button"
                  ga-event-action="Trimite rezervarea"
                  ga-event-label="booking step 3"
                  ga-event-value={Math.floor(prices.totalPriceWithDiscount)}
                >
                  Trimite rezervarea
                </Button>
                <Loader className={isLoading ? "loading" : ""} />
              </div>
              {submitError && <label className="error">{submitError}</label>}
            </div>
          )}
        />
      </div>
    );
  }
}
